.image-list {
  display: flex;
  flex-direction: row;
  list-style: none;
  width: 130%;
  /* max-width: 1136px;
  margin: 0 auto;
  align-items:baseline;
  padding: 0; */
  justify-content: space-between;
  padding: unset;
}

.image-list > li > .list-item {
  /* width: 260px; */
  margin-left: 0;
  padding-left: 0;
}

.image-list > li > .list-item a:link {
  text-decoration: none;
}

/* visited link */
.image-list > li > .list-item a:visited {
  color: #696969;
}

/* mouse over link */
.image-list > li > .list-item a:hover {
  color: #696969;
}

/* selected link */
.image-list > li > .list-item a:active {
  color: #696969;
}

.list-item-title {
  color: #202020;
  font-size: 18px;
  font-family: Source Sans Pro, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 600;
  text-transform: uppercase;

}

.list-item-description {
  display: block;
  font-size: 14px;
  margin-top: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: uppercase;
  white-space: nowrap;
  width: 260px;
}

.image-list > li > .list-item img {
    /* width:260px;
    height:180px; */
    overflow: cover;
}

.list-item span.cmp-image__title {
  display: none;
}

.image-list .tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
  margin: auto;
}

/* Tooltip text */
.image-list .tooltip .tooltiptext {
  visibility: hidden;
  width: 100%;
  top:40%;
  background-color: rgba(0, 0, 0, .45);
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.image-list .tooltip:hover .tooltiptext {
  visibility: visible;
}

